
import {gsap} from 'gsap';


export const onFadeInOriginT = (...rest) =>{
    let tl = gsap.timeline();
    tl.staggerFrom([rest[0].elem],rest[0].duration,
        {x:rest[0].x,y:rest[0].y,opacity:rest[0].opacity},
        rest[0].stagger,rest[0].relativeT);
    return tl;
}

export const framer = ()=>{


    const transition = {
        duration: 0.8,
        ease: [0.6, -0.05, 0.01, 0.9]
    }

    const maskAnimation = {
        initial: {
            width: "100%"
        },
        animate: {
            width: "0%"
        }
    }

    const worksTitleParent = {
        initial: {
            opacity: 1,
        },
        animate: {
            opacity:1,
            transition: {
                duration: 1,
                staggerChildren: 0.2,
                delayChildren: 3,
            }
        }
    }

    const parent = {
        initial: {
            opacity: 1,
        },
        animate: {
            opacity:1,
            transition: {
                staggerChildren: 0.1,
                delayChildren: 3,
            }
        }
    }

    const textReveal = {
        hidden: {
            y: "100%",
        },
        show: {
            y: "0%",
            transiton:{
                
            }
        }
    }
    const textMask = {
        initial: {
            y: "-50%",
        },
        animate: {
            y: "0%",
        }
    }

    const opacityReveal = {
        initial: {
            y: "120%",
            opacity: 0
        },
        animate: {
            y: "0",
            opacity: 1
        }
    }
    const animationVariants = {
        show: (delay = 1) => ({
          y: 0,
          opacity: 1,
          transition: {
            duration: 0.5,
            delay,
          },
        }),
        hide: (delay: 0 | 0.3 = 0) => ({
          y: 30,
          opacity: 0,
          transition: {
            duration: 0.5,
            delay: 0.3 - delay, // the inverse of the show delay
          },
        }),
      };
      
    const showIn = {
        hidden: {
          opacity: 0
        },
        default: {
          transition: {
            ease: "easeInQuad",
            delayChildren: 1,
            staggerChildren: 1
          }
        },
        visible: {
          opacity: 1,
          transition: {
            ease: "easeInQuad",
            delayChildren: 1,
            staggerChildren: 1
          }
        }
      };
      const showInWrap = {
        initial: { y: "100%" },
        hidden: { y: "100%" },
        active: { y: "0%" },
        visible: (i) => ({
          y: "0%",
          transition: {
            ease: "easeInOutQuint",
            delay: i * 0.6
          }
        })
      };
    return {
        transition,
        maskAnimation,
        parent,
        textReveal,
        opacityReveal,
        worksTitleParent,
        textMask,
        showIn,
        showInWrap,
        animationVariants
    }
}